
body {
  background: black;
}
.challenge {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.sidebar {
  margin-top: 10px;
  width: 250px;
  height: 790px;
}
.box-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 auto;
          flex: 0 1 auto;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 900px;
}
.box:not(:last-of-type) {
  margin-right: 10px;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s ease;
  transition: opacity .5s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.child-view {
  position: absolute;
  -webkit-transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  margin-left: 282px;
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}
.router-link-active {
  color: #bad61e;
}
.green-text {
  color: #bad61e;
}
.big {
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 800;
}
.tiny {
  display: inline-block;
  font-size: 12px;
  line-height: 1.5;
  padding: 16px 0;
}
.box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  margin-top: 10px;
  height: 240px;
  width: 250px;
  border: solid 1px #bad61e;
}
.box--primary {
    background: white;
    color: black;
}
.box--secondary {
    color: white;
    background: black;
}
.box--wide {
    width: 512px;
    background: #bad61e;
    color: black;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.box--wide .copy {
      padding: 48px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.box--wide .copy .big {
        margin-right: 48px;
}
.copy {
  padding: 0 20px;
  font-size: 20px;
}
.cta {
  padding: 18px 0;
  text-align: left;
}
.cta a .is-black {
    color: white;
    background: black;
}
.Button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
  border: 0;
  overflow: visible;
  font: inherit;
  font-weight: 600;
  color: #333;
  text-transform: none;
  display: inline-block;
  padding: 0 32px;
  background: #ececec;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 3px;
  -webkit-transition: all 0.25s easeInOutCubic;
  transition: all 0.25s easeInOutCubic;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.11), 0 4px 6px 0 rgba(0, 0, 0, 0.08);
  line-height: 36px;
  height: 36px;
  font-size: 16px;
  text-decoration: none;
  text-align: left;
  white-space: nowrap;
}
.Button--primary {
    background: black;
    color: #bad61e;
}
.Button--secondary {
    background: #bad61e;
    color: black;
}



